import React from 'react';

const NoMatchPage = () =>
  <main>
    <h1>Sidan kunde inte hittas.</h1>
    <p>
      Något måste ha gått fel, men <b>prova gärna länkarna ovan</b> så
      löser det nog sig trots allt.
    </p>
  </main>

export default NoMatchPage;



